import Swiper from "swiper";
import { Navigation } from "swiper/modules";

function sliderGallery() {
  new Swiper('.swiper-gallery .swiper', {
    modules: [Navigation],
    slidesPerView: 1,
    grabCursor: true,
    speed: 1000,
    spaceBetween: 20,
    loop: true,
    navigation: {
      prevEl: '.swiper-button-prev',
      nextEl: '.swiper-button-next',
    }
  });
}
sliderGallery();