import ScrollReveal from "scrollreveal";

function scrollInit() {
  const options = {
    delay: 500,
    distance: "20%",
    origin: "bottom",
    opacity: 0,
    mobile: false,
  };
  const revealItemsByScale = {
    delay: 300,
    opacity: 1,
    afterReveal: function (el) {
      $(el).addClass('active');
    }
  };
  const revealBorderOptions = {
    delay: 300,
    opacity: 1,
    afterReveal: function (el) {
      $(el).addClass('active');
    }
  };
  ScrollReveal().reveal(".reveal", options);
  ScrollReveal().reveal(".reveal-border", revealBorderOptions);
  ScrollReveal().reveal(".reveal-scaled", revealItemsByScale);
}
scrollInit();