import Swiper from "swiper";
import { Pagination } from "swiper/modules";

function sliderBrands() {
  new Swiper('.swiper-brands', {
    modules: [Pagination],
    spaceBetween: 10,
    slidesPerView: 1,
    breakpoints: {
      768: {
        slidesPerView: 2
      },
      1280: {
        slidesPerView: 4
      },
      1635: {
        slidesPerView: 4
      }
    },
    pagination: {
      el: '.swiper-pagination',
      clickable: true
    },
  });
}
sliderBrands();