import Swiper from "swiper";
import { Navigation } from "swiper/modules";

function sliderProducts() {
  new Swiper('.swiper-products .swiper', {
    modules: [Navigation],
    slidesPerView: 1.5,
    grabCursor: true,
    speed: 1000,
    spaceBetween: 10,
    loop: true,
    breakpoints: {
      768: {
        slidesPerView: 2.5,
        spaceBetween: 10,
      },
      1180: {
        slidesPerView: 4,
        spaceBetween: 27,
      },
    },
    navigation: {
      prevEl: '.swiper-products .swiper-button-prev',
      nextEl: '.swiper-products .swiper-button-next',
    }
  });
}
sliderProducts();