import lightGallery from "lightgallery";
import lgPager from 'lightgallery/plugins/pager'
import { clearQueueScrollLocks, disablePageScroll, enablePageScroll } from "scroll-lock";

function gallery() {
  let galleries = $('.gallery');
  Array.from(galleries).forEach(function(gallery) {
    lightGallery(gallery, {
      licenseKey: 'UNLICENSED',
      controls: true,
      plugins: [lgPager]
    });
  });
  $('.swiper-includes .swiper-slide, .lg-outer').on('click', function() {
    if ($('html').hasClass('lg-on')) {
      disablePageScroll();
    } else {
      clearQueueScrollLocks();
      enablePageScroll();
    }
  });
  $('.swiper-includes .swiper-slide').on('click', function() {
    $(this).next().find('img').trigger("click");
    $(this).next().next().find('img').trigger("click");
  });
}
gallery();