import Swiper from "swiper";
import { Navigation } from "swiper/modules";

function sliderGallery() {
  new Swiper('.swiper-includes .swiper', {
    modules: [Navigation],
    slidesPerView: 1.5,
    grabCursor: true,
    speed: 1000,
    spaceBetween: 20,
    autoplay: {
      delay: 4000,
      disableOnInteraction: false
    },
    loop: true,
    breakpoints: {
      1180: {
        slidesPerView: 6,
      },
      1635: {
        spaceBetween: 32,
      }
    },
    navigation: {
      prevEl: '.swiper-includes .swiper-button-prev',
      nextEl: '.swiper-includes .swiper-button-next',
    }
  });
}
sliderGallery();