import { disablePageScroll, enablePageScroll } from 'scroll-lock';

export default function toggleMenu() {
  let button = $('.header__burger');
  let menu = $('.header nav');
  let items = $('.header nav a, .header__logo');
  let trigger = $('.header__trigger');
  let list = $('.header__list');
  let header = $('.header');
  let scrollableElement = document.querySelector('.header__nav');
  button.on('click', function() {
    if ($(this).hasClass('active')) {
      menu.removeClass('active');
      enablePageScroll();
      $(this).removeClass('active');
    } else {
      menu.addClass('active');
      disablePageScroll(scrollableElement);
      $(this).addClass('active');
    }
    if ($(window).scrollTop() == 0 || $(window).scrollTop() > 0 ) {
      header.addClass('active');
    } else {
      header.removeClass('active');
    }
  });
  $(window).on('resize', function() {
    if ($(window).width() >= 1180 && button.hasClass('active')) {
      trigger.on('click', function() {
        if ($(this).hasClass('active')) {
          $(this).removeClass('active');
          list.removeClass('active');
        } else {
          $(this).addClass('active');
          list.addClass('active');
        }
      });
    }
  });
  Array.from(items).forEach((item) => {
    $(item).on('click', function() {
      setTimeout(function() {
        button.removeClass('active');
        menu.removeClass('active');
        enablePageScroll();
      }, 300);
    });
  });
  $(window).on('resize', function() {
    if ($(window).width() >= 1180 && button.hasClass('active')) {
      button.removeClass('active');
      menu.removeClass('active');
      enablePageScroll();
    }
  });
}
toggleMenu();